import React from 'react';
import { Plugin, NodeSelectToolbarItem } from '@ali/4ever-cangjie';
import { MobileIconComment } from '@ali/4ever-component';
import { CommentLocale as ILocale } from '../types';

const createGetNodeSelectToolbarItems = (locale: ILocale): Plugin['getNodeSelectToolbarItems'] => {
  return (props, _, next) => {
    const commentItem: NodeSelectToolbarItem = {
      key: 'comment',
      icon: <MobileIconComment color="rgb(239, 171, 25)" />,
      action: 'commentSelectedNodes',
      priority: 1,
      title: locale.comment || 'Comment',
      disabled: !props.isSequent,
    };
    const nextItems = next();
    return [commentItem, ...(Array.isArray(nextItems) ? nextItems : [])];
  };
};

export default createGetNodeSelectToolbarItems;
